
import { computed, defineComponent } from 'vue'
import SvgIcon from '@/components/SvgIcon'
import { useStore } from 'vuex'

export default defineComponent({
  name: 'User',
  components: {
    SvgIcon
  },
  setup() {
    const store = useStore()
    return {
      userInfo: computed(() => store.state.user.userInfo)
    }
  }
})
